import bcryptjs from 'bcryptjs';

class ValidateStorage {
    constructor(secret='secret phrase') {
        this.secret = secret

    }
    // Devuelve el token de un objeto
    createToken(obj) {
        if(obj instanceof Object) {
            const token_obj = {}
            Object.keys(obj).map(field => {
                if(field !== 'token')
                    token_obj[field] = obj[field]
            })                 
            return bcryptjs.hashSync(JSON.stringify(token_obj))
        } else 
            throw new Error('El parametro enviado no es un objeto')
    }
    verifyToken(obj) {
        if(obj instanceof Object) {
            if(!('token' in obj)) 
                throw new Error('El objeto no tiene token encryptado')
            else {
                // console.log(this.createToken(obj))
                const token = this.createToken(obj)
                const token_obj = {}
                Object.keys(obj).map(field => {
                    if(field !== 'token')
                        token_obj[field] = obj[field]
                }) 
                return bcryptjs.compareSync(JSON.stringify(token_obj),obj.token)
            }
        } else 
            throw new Error('El parametro enviado no es un objeto')
    }
}

export default ValidateStorage